.Profile {
  margin-top: 60px;
  .container {
    display: flex;
    gap: 20px;
  }
  .left-part {
    flex: 2;
    overflow-y: auto;
  }
  .right-part {
    flex: 1;
    margin-top: 20px;
  }

  .profile-card {
    border: 1px solid var(--border-color);
    border-radius: 4px;
    text-align: center;
    padding: 40px;

    .user-img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
    }

    .user-name,
    .follower-info {
      margin-top: 12px;
    }

    .bio{
      margin-top: 6px;
    }

    .follower-info {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 40px;
    }

    .follow,
    .update-profile {
      margin-top: 20px;
      display: inline-block;
    }
    .follow-link{
      color: var(--accent-color);
    }
  }
}
